.switcher {
  border: none;
  grid-template-columns: 1fr 1fr;
  padding: 2px;
  display: grid;
  position: relative;
}

.switcher__legend {
  opacity: 0;
  pointer-events: none;
  position: absolute;
}

.switcher__radio {
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  width: 18px;
  height: 16px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 24px;
  margin: 0;
}

.switcher__status {
  z-index: -1;
  border: 1px solid var(--switcher-color);
  background-color: var(--white-color);
  background-image: url("l-radio.638e2509.svg");
  background-position: 2px 50%;
  background-repeat: no-repeat;
  background-size: 16px;
  border-radius: 18px;
  transition: all .25s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.switcher__radio--lignt:checked ~ .switcher__status {
  background-position: 2px 50%;
}

.switcher__radio--dark:checked ~ .switcher__status {
  border: 1px solid var(--white-color);
  background-image: url("d-radio.c04f23a5.svg");
  background-position: right 2px center;
  background-color: var(--switcher-color);
}

.switcher-icon {
  stroke: var(--theme-color);
}

.switcher-icon--light {
  margin-right: 8px;
}

.switcher-icon--dark {
  margin-left: 8px;
}

@media screen and (min-width: 1280px) {
